.aboutMe {
  text-align: center;
  font-family: Arial, sans-serif;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url(../../icons/background/Header1.jpg) no-repeat cadetblue center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.footerUrls {
  filter: invert(1);
  padding: 1em;
}
.title h1 {
  font-size: 72px;
  margin-block-start: 0;
  margin-bottom: 20px;
  color: rgb(255, 130, 6);
}

.title img {
  height: 200px;
  margin-top: 150px;
}

.Info {
  font-size: 24px;
  line-height: 1.5;
  margin-bottom: 20px;
  width: 80%;
  color: rgb(0, 217, 255);
  text-align: center;
}

@media (max-width: 767px) {
  .title {
    font-size: 36px;
  }
  .Info {
    font-size: 16px;
  }
}
