.projectsSection {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: fit-content;
  padding-top: 50px;
  background: url(../../icons/background/Header1.jpg) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.projectsIntroContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
}

.projectsHeader {
  font-size: 2rem;
  margin-top: 3rem;
  color: rgb(255, 130, 6);
}

.projectsDisclaimer {
  max-width: 800px;
  font-size: 1.2rem;
  margin: auto;
  margin-bottom: 6rem;
  padding: 10px;
  color: whitesmoke;
}

.projectsContainer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 90%;
}

@media (max-width: 768px) {
  .projectsSection {
    flex-direction: column;
    width: 100%;
    align-items: center;
  }
  .projectsIntroContainer {
    width: 90%;
    align-items: center;
  }
}
